import React, { useEffect, useState } from 'react';

import { chart } from '../depedances/chartConfig';
import { generateNewDatasAndUpdateChart, updateCurrentPrice, updateFlag, updateGain, updateTickPrice } from '../depedances/gameLoop';
import { showFile } from '../depedances/txtToObject';

let index = 0
let dynamicCurrentPrice = [0]
const fee = 0.004
let dynamicValues = { sellShort: 'Buy Short', longSell: 'Buy Long', orderPrice: 0, tickPriceData: 0 ,playerCrypto : 0 , playerCurrentFee : 0}
const Playground = ({ stateDatas, setStateDatas }) => {
    const [cryptoDatas, setCryptoDatas] = useState()
    const [currentPrice, setCurrentPrice] = useState(0)
    const [sellShort, setSellShort] = useState('Buy Short')
    const [longSell, setLongSell] = useState('Buy Long')
    const [flagColor, setFlagColor] = useState('transparent')
    const [playerCrypto, setPlayerCrypto] = useState(0)
    const [orderPrice, setOrderPrice] = useState(0)
    const [gain ,setGain] = useState(0)
    
    useEffect(() => {
        showFile(setCryptoDatas)
    }, [])
    useEffect(() => {
        if (cryptoDatas) {
            //console.log(cryptoDatas)
            let bufferDatas = []
            for (let i = 0; i < 500; i++) {
                bufferDatas.push(cryptoDatas[i])
            }
            //onsole.log(bufferDatas)
            updateCurrentPrice(bufferDatas, setCurrentPrice, dynamicCurrentPrice)
            chart(bufferDatas, dynamicCurrentPrice[0])
            //startGameLoop(cryptoDatas)

            loopControler()


        }
    }, [cryptoDatas])
    const loopControler = () => {
        const interval = setInterval(() => {
            index++
            let currentData = []
            generateNewDatasAndUpdateChart(currentData, cryptoDatas, index, dynamicValues)
            updateCurrentPrice(currentData, setCurrentPrice, dynamicCurrentPrice)
            updateTickPrice(dynamicValues, dynamicCurrentPrice)
            updateFlag(dynamicCurrentPrice, dynamicValues, setFlagColor)
            updateGain(setGain ,dynamicCurrentPrice, dynamicValues,fee)
        }, 200)
        return () => clearInterval(interval);
    }

    const longOrSell = () => {
        if (stateDatas.playerMonney > 0) {
            dynamicValues.playerCurrentFee = (stateDatas.playerMonney * fee)
            const transactionResult = (stateDatas.playerMonney - (stateDatas.playerMonney * fee)) / currentPrice
            setPlayerCrypto(transactionResult)
            dynamicValues.playerCrypto = transactionResult
            setStateDatas({ playerMonney: 0, cryptoUnit: stateDatas.cryptoUnit })
            setSellShort('Sell')
            dynamicValues.sellShort = 'Sell'
            setLongSell('')
            dynamicValues.longSell = ''
            setOrderPrice(currentPrice)
            dynamicValues.orderPrice = currentPrice
        } else if (stateDatas.playerMonney === 0 && playerCrypto < 0) {
           
            setStateDatas({ playerMonney: - (playerCrypto * currentPrice - (playerCrypto * currentPrice * fee)), cryptoUnit: stateDatas.cryptoUnit })
            setPlayerCrypto(0)
            dynamicValues.playerCrypto = 0
            setSellShort('Buy Short')
            dynamicValues.sellShort = 'Buy Short'
            setLongSell('Buy Long')
            dynamicValues.longSell = 'Buy Long'
            setOrderPrice(0)
            dynamicValues.orderPrice = 0
        }
    }
    const shortOrSell = () => {
        if (stateDatas.playerMonney === 0 && playerCrypto > 0) {
            
            setStateDatas({ playerMonney: playerCrypto * currentPrice - ((playerCrypto * currentPrice) * fee), cryptoUnit: stateDatas.cryptoUnit })
            setPlayerCrypto(0)
            dynamicValues.playerCrypto = 0
            setSellShort('Buy Short')
            dynamicValues.sellShort = 'Buy Short'
            setLongSell('Buy Long')
            dynamicValues.longSell = 'Buy Long'
            setOrderPrice(0)
            dynamicValues.orderPrice = 0
        } else if (stateDatas.playerMonney > 0 && playerCrypto === 0) {
            dynamicValues.playerCurrentFee = (stateDatas.playerMonney * fee)
            const transactionResult =-(stateDatas.playerMonney - (stateDatas.playerMonney * fee)) / currentPrice
            setPlayerCrypto(transactionResult)
            dynamicValues.playerCrypto = transactionResult
            setStateDatas({ playerMonney: 0, cryptoUnit: stateDatas.cryptoUnit })
            setSellShort('')
            dynamicValues.sellShort = ''
            setLongSell('Sell')
            dynamicValues.longSell = 'Sell'
            setOrderPrice(currentPrice)
            dynamicValues.orderPrice = currentPrice
        }
    }
    return (
        <main className=" playground-main">
            <section>
                <div>
                    <h2 className="current-price">current price :{currentPrice}$</h2>
                    <h2 className={"order " + flagColor}  >Order at : {orderPrice}</h2>
                    <h2 className = "gain" >Gain : {gain}$</h2>
                </div>
                <div>
                    <h2 className="user-money">monney : {stateDatas.playerMonney} $ / {playerCrypto} {stateDatas.cryptoUnit}</h2>
                    <h2> fee : 0.004% </h2>
                </div>
            </section>
            <div className="chart">
                <canvas id="line-chart" width="800" height="450"></canvas>
            </div>
            <section className="buy-sell">
                <div className="sell-btn btn" onClick={longOrSell}>{longSell}</div>
                <div className="buy-btn btn" onClick={shortOrSell}>{sellShort}</div>
            </section>

        </main>
    );
}

export default Playground;
