
import { chartUpdate } from "./chartConfig"

export const generateNewDatasAndUpdateChart = (currentData, data, index,dynamicValues) => {
    for (let i = index; i < (500 + index); i++) {
        currentData.push(data[i])
    }
    currentData.slice(index, 500 + index)
    chartUpdate(currentData, dynamicValues.tickPriceData)
}

export const updateCurrentPrice = (currentData, setCurrentPrice, dynamicCurrentPrice) => {
    const lastPrice = currentData[currentData.length - 1]
    dynamicCurrentPrice[0] = lastPrice
    setCurrentPrice(lastPrice)

}
export const updateFlag = (dynamicCurrentPrice, dynamicValues, setFlagColor) => {
    if (dynamicValues.longSell === '') {
      
        if (dynamicCurrentPrice[0] > dynamicValues.orderPrice) {
            setFlagColor('green')
        }
        if (dynamicCurrentPrice[0] <= dynamicValues.orderPrice) {
            setFlagColor('red')
        }
    } else if (dynamicValues.sellShort === '') {
        if (dynamicCurrentPrice[0] > dynamicValues.orderPrice) {
            setFlagColor('red')
        }
        if (dynamicCurrentPrice[0] <= dynamicValues.orderPrice) {
            setFlagColor('green')
        }
    } else  {
        setFlagColor('transparent')
    }
}
export const updateTickPrice = (dynamicValues, dynamicCurrentPrice) => {
    if (dynamicValues.orderPrice === 0) {
        dynamicValues.tickPriceData = dynamicCurrentPrice[0]
    } else {
        dynamicValues.tickPriceData = dynamicValues.orderPrice
    }

}
export const updateGain = (setGain ,dynamicCurrentPrice, dynamicValues,fee) => {
    if (dynamicValues.orderPrice > 0 && dynamicValues.longSell === ''){
        setGain(((dynamicCurrentPrice[0]-dynamicValues.orderPrice)*dynamicValues.playerCrypto) - dynamicValues.playerCurrentFee - (dynamicValues.playerCrypto * dynamicCurrentPrice[0] * fee)) 
    }else if (dynamicValues.orderPrice > 0 && dynamicValues.sellShort === ''){
        setGain(((dynamicValues.orderPrice - dynamicCurrentPrice[0])*(-dynamicValues.playerCrypto)) - dynamicValues.playerCurrentFee -( - dynamicValues.playerCrypto * dynamicCurrentPrice[0] * fee) )
    }else{
        setGain(0)
    }


}